import {makeObservable, observable} from "mobx";

export default class Post
{
    uplIdx;
    uplTitle;
    uplContent;
    open=false;
    constructor(uplIdx,uplTitle,uplContent) {
        this.uplIdx = uplIdx;
        this.uplTitle = uplTitle;
        this.uplContent = uplContent;
        this.open = false;
        makeObservable(this,{
            uplIdx:observable,
            uplTitle:observable,
            uplContent:observable,
            open:observable,
        });
    }
}