import {action, makeObservable, observable} from "mobx";

export default class PostModalStore
{
    rootStore;

    title='';
    content = '';

    constructor(root) {
        this.rootStore = root;
        this.title ='';
        this.content ='';

        makeObservable(this,{
            title:observable,
            content:observable,
            titleChange:action,
            contentChange:action
        })
    }

    titleChange(value)
    {
        this.title = value;
    }

    contentChange(value)
    {
        this.content = value;
    }
}